.btn {
  box-sizing: border-box;
  padding: 10px 15px;
  font-size: 18px;
  color: #fff;
  border-radius: 3px;
  background-size: 200%;
  background-position: 100% 0;
  transition: 0.2s all ease;
  border: 1px solid #e86aad;
  outline: none;
  cursor: pointer;
  &-active {
    background: #e86aad;
  }
  &-outline {
    background: transparent;
    color: #425674;
  }
  &:first-child {
    border-radius: 3px 0 0 3px;
  }
  &:last-child {
    border-radius: 0 3px 3px 0;
  }
}

