.app-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 2px 10px rgba(78, 127, 222, 0.2);
}

.app-header h1 {
  color: #425674;
  font-size: 26px;
  font-weight: 700;
}

.app-header h2 {
  font-size: 1rem;
  font-weight: 300;
  color: grey;
}
