.app-list-item {
  font-size: 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  color: #425674;
}

.app-list-item button {
  width: 44px;
  height: 44px;
  margin: 0 6px;
  font-size: 17px;
  border-radius: 50%;
  background: #f8fafd;
  box-shadow: 0px 2px 10px rgba(78, 127, 222, 0.1);
  border: 1px solid rgba(66, 86, 116, 0.2);
  cursor: pointer;
  position: relative;
  z-index: 999;
  transition: 0.3s all ease;
  &:hover {
    border-color: #e86aad;
    box-shadow: none;
    color: #425674;
  }
}

.app-list-item button:focus {
  outline: none;
}
.app-list-item .btn-star {
  color: rgba(106, 133, 175, 0.3);
}
.app-list-item .btn-trash {
  color: rgba(106, 133, 175, 0.3);
}
.app-list-item .fa-heart {
  width: 35px;
  text-align: center;
  font-size: 18px;
  color: #f78e9b;
  transition: 0.3s all ease;
  transform: scale(0);
  opacity: 0;
}

.app-list-item-label {
  display: block;
  cursor: pointer;
  user-select: none;
  transition: 0.5s all;
  max-width: 600px;
}

.app-list-item-label:hover + div > i.fa-heart {
  opacity: 0.1;
  transform: scale(1.5);
}

.app-list-item:hover.like .fa-heart {
  opacity: 1;
  transform: scale(1);
}

.app-list-item.like .fa-heart {
  transform: scale(1);
  opacity: 1;
}

.list-group-item {
  box-shadow: 0px 2px 10px rgba(78, 127, 222, 0.1);
  border-radius: 6px;
  background: #f8fafd;
}

.app-list-item.favorite .btn-star {
  color: #e86aad;
}
