.search-panel {
  margin: 1rem 0;
}

.search-panel .search-input {
  box-sizing: border-box;
  width: auto;
  flex-grow: 1;
  margin-right: 10px;
  background-color: #fafafc;
  outline: none;
  padding: 10px 15px;
  color: #293951;
  font-size: 18px;
  border: 1px solid rgba(92, 121, 162, 0.15);
  box-shadow: inset 0px 2px 6px rgba(92, 121, 162, 0.3);
  border-radius: 3px;
  &:focus {
    border-color: #f78e9b;
  }
  &::placeholder {
    color: #6a86af;
  }
}
