.bottom-panel {
  display: flex;
  background-color: #fff;
  padding: 15px;
  margin-top: 20px;
  border-radius: 6px;
  box-shadow: 0px 2px 10px rgba(78, 127, 222, 0.2);
  &-button {
    padding: 10px 15px;
    font-size: 18px;
    color: #fff;
    background: linear-gradient(90deg, #e86aad 50%, #5cbafe 100%);
    text-shadow: 0 2px 10px rgba(0, 0, 0, 0.3);
    border-radius: 3px;
    background-size: 200%;
    background-position: 100% 0;
    transition: 0.2s all ease;
    border: none;
    outline: none;
    cursor: pointer;
    &:hover {
      background-position: 0 0;
    }
    &:active {
      transform: translateY(2px);
    }
    &.muted {
      opacity: 0.5;
      cursor: no-drop;
    }
  }
}

.bottom-panel .new-post-label {
  box-sizing: border-box;
  width: auto;
  flex-grow: 1;
  margin-right: 10px;
  background-color: #fafafc;
  outline: none;
  padding: 10px 15px;
  color: #293951;
  font-size: 18px;
  border: 1px solid rgba(92, 121, 162, 0.15);
  box-shadow: inset 0px 2px 6px rgba(92, 121, 162, 0.3);
  border-radius: 3px;
  &:focus {
    border-color: #f78e9b;
  }
  &::placeholder {
    color: #6a86af;
  }
}
